.header{
    position: fixed;
    top: 0;width: 100%;
    height: 50px;
    background-color: #fff;
    z-index: 1000;
    border-bottom: 3px solid #f3ba3c;
    .navList{
        display: flex;
        margin-right: 20px;
        justify-content: flex-end;
        height: 100%;
        align-items: center;
        gap: 20px;
        font-weight: 500;
        span{
            padding: 5px 15px;
            border-radius: 5px;
            // background-color: gray;
            cursor: pointer;
            i{
                margin-left: 8px;
            }
        }
    }
}